'use client';

import React, { createContext, useContext, useEffect, useState } from 'react';

// Simplified context - always returns static values
type ThemeContextType = {
    theme: string;
    toggleTheme: () => void;
};

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

export function ThemeProvider({ children }: { children: React.ReactNode }) {
    // We strictly enforce a single theme now.
    // No localStorage reading, no data-attribute toggling needed
    // as globals.css handles the single theme at :root.

    const toggleTheme = () => {
        // No-op
        console.log('Theme toggling is disabled.');
    };

    return (
        <ThemeContext.Provider value={{ theme: 'custom', toggleTheme }}>
            {children}
        </ThemeContext.Provider>
    );
}

export function useTheme() {
    return { theme: 'custom', toggleTheme: () => { } };
}
